import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import Download from '@/views/Download'
import HomeSearch from '@/views/HomeSearch'
import HomeIndex from '@/views/HomeIndex'
import BlogAdd from '@/views/BlogAdd'
import BlogShow from '@/views/BlogShow'

import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

Vue.use(mavonEditor)
Vue.use(VueRouter)



const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    name: 'add-blog',
    path: '/add-blog',
    component: BlogAdd,
  },
  {
    name: 'show-blog',
    path: '/show-blog/:id',
    component: BlogShow,
  },
  {
    name: 'register',
    path: '/register',
    component: () => import('@/views/Register'),
  },
  {
    name: 'login',
    path: '/login',
    component: () => import('@/views/Login'),
  },
  {
    path: '/my-page',
    component: () => import('@/views/MyPage'),
    children: [
      {
        name: 'my-page',
        path: '',
        component: () => import('@/views/MyPageIndex'),
      }
    ]
  }
]

const headerNav = [
  {
    path: '/download',
    component: Download,
    value: '下载',
    name: 'download'
  },
  {
    path: '/home',
    component: Home,
    value: '首页',
    children: [
      {
        path: '',
        name: 'home',
        component: HomeIndex
      },
      {
        name: 'search',
        path: '/home/search',
        component: HomeSearch
      }

    ]
  }
]







const router = new VueRouter({
  routes: [...routes, ...headerNav]
})



router.beforeEach((to, from, next) => {
  let isLogin = localStorage.getItem("BLOG_TOKEN") ? true : false;
  if (to.path === "/login" || to.path === '/register' || to.name === "show-blog") {
    next()
  } else {
    isLogin ? next() : next("/login")
  }
})

export default router;

export { headerNav }