<template>
    <div class="blog-add">
        <div class="title-nav">
            <button @click.stop="$router.go(-1)" class="back">返回</button>
            <input v-model="title" type="text" placeholder="请输入标题!" />
            <el-form ref="form" label-width="40px">
                <el-form-item label="分类">
                    <el-select v-model="acticleType" placeholder="请选择">
                        <el-option label="前端" value="qianduan"></el-option>
                        <el-option label="后端" value="houduan"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <button
                @click.stop="handleSave"
                v-show="type === 'add'"
                class="save"
            >
                保存草稿
            </button>
            <button @click.stop="handlePublic" class="public">
                {{ type === "add" ? "发布文章" : "保存修改" }}
            </button>
        </div>
        <div class="edit">
            <mavon-editor
                @change="handleChange"
                v-model="value"
                codeStyle="atelier-plateau-light"
                class="edit-main"
            />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            value: "",
            title: "测试",
            markdown: "",
            html: "",
            type: "add",
            blogID: "",
            acticleType: "qianduan"
        };
    },
    created() {
        let { id, type } = this.$route.query;
        if (id && type === "edit") {
            this.type = "edit";
            this.getBlogById(id);
        } else {
            this.markdown = "";
            this.html = "";
        }
    },
    methods: {
        handleChange(markdown, html) {
            this.markdown = markdown;
            this.html = html;
        },
        handleSave() {
            console.log(this.title);
            console.log(this.markdown, this.html);
        },
        async handlePublic() {
            let data = null;
            if (this.type === "add") {
                data = await this.$store.dispatch("AddBlog", {
                    title: this.title,
                    type: this.acticleType,
                    markdown: this.markdown,
                    html: this.html
                });
            } else if (this.type === "edit") {
                data = await this.$store.dispatch("editBlog", {
                    title: this.title,
                    markdown: this.markdown,
                    html: this.html,
                    type: this.acticleType,
                    id: this.blogID
                });
            }

            if (data) {
                this.$message({
                    message: "发布成功",
                    type: "success"
                });
                this.$router.replace("/show-blog/" + data._id);
            }
        },
        async getBlogById(id) {
            let blog = await this.$store.dispatch("getBlogById", id);
            this.blogID = blog._id;
            this.value = blog.markdown;
            this.title = blog.title;
        }
    }
};
</script>

<style lang="scss" scoped>
.title-nav {
    height: 55px;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    padding: 0 24px;
    button.back {
        outline: none;
        font-size: 1rem;
        border: 0;
        background-color: #f3f4f5;
        padding: 0.5rem;
        transition: $transition-base;
        &:hover {
            background-color: $uni-color-error;
        }
    }
    button {
        white-space: nowrap;
        cursor: pointer;
        border-radius: $uni-border-radius-sm;
    }
    input {
        height: 36px;
        width: 100%;
        margin: 0 16px;
        padding: 0 0.5rem;
    }
    .el-form {
        margin: 0 16px;
        height: 40px;
        .el-form-item {
            margin-bottom: 0;
        }
    }
    button.save {
        height: 36px;
        width: 98px;
        margin-right: 16px;
        padding: 0 16px;
        white-space: nowrap;
        color: $uni-color-error;
        border: 1px solid $uni-color-error;
        outline: none;
        &:hover {
            background-color: rgb(212, 158, 158);
        }
    }

    button.public {
        margin-right: 16px;
        padding: 0 16px;
        height: 36px;
        width: 98px;
        background-color: $uni-color-error;
        color: #fff;
        outline: none;
        border: 0;
        &:hover {
            background-color: rgb(168, 0, 0);
        }
    }
}

.edit {
    height: 100%;
    .edit-main {
        height: calc(100vh - 55px);
    }
}
</style>