<template>
    <nav class="nav">
        <ul @click.stop="handleItem" class="nav-container">
            <li
                class="nav-item"
                v-for="(item, index) in leftNavList"
                :key="index"
                :data-key="item.id"
                :class="{ 'nav-active': item.isActive }"
            >
                {{ item.title }}
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
    props: {
        leftNavList: {
            type: Array,
            default() {
                return [];
            }
        },
        handleItem: Function
    }
};
</script>

<style lang="scss" scoped>
.nav {
    margin-right: 12px;
    width: 96px;
    height: 100%;
    font-size: 14px;
    position: relative;

    .nav-container {
        position: fixed;
        background-color: #fff;
        display: flex;
        width: 96px;
        flex-direction: column;
        .nav-item {
            width: 100%;
            height: 30px;
            line-height: 30px;
            border-bottom: 1px solid #fff;
            text-align: center;
            cursor: pointer;
            @extend .hover-nav;
            
            &:first-of-type {
                border-top: 1px solid #fff;
            }
        }
        .nav-active {
            background-color: $uni-color-error;
            color: #fff;
        }
    }
    ol,
    ul {
        list-style: none;
    }
}
</style>