<template>
    <div class="blog-show">
        <HomeHeader class="show-header" />
        <div class="article">
            <div class="article-header">
                <div class="title">
                    {{ blog.title }}
                </div>
                <div class="article-author-edit">
                    <div class="article-author">
                        <div class="img">
                            <img
                                :src="baseIMGURL + userid + '?byuserid=1'"
                                class="ueser-head"
                            />
                        </div>
                        <div class="user-name-time">
                            <span class="user-name">{{ blog.username }}</span>
                            <span class="time">{{
                                blog.createdAt | date1
                            }}</span>
                        </div>
                        <span class="views iconfont icon-Icon-guankanliang">{{
                            blog.views
                        }}</span>
                        <span
                            @click.stop="handleLiked"
                            :class="[
                                'like',
                                'iconfont',
                                liked ? 'icon-shoucang' : 'icon-shoucang1'
                            ]"
                        >
                            {{ likes.length }}</span
                        >
                    </div>
                    <div
                        v-show="user._id === blog.userid"
                        @click.stop="handleEdit"
                        class="article-edit"
                    >
                        编辑
                    </div>
                </div>
            </div>
            <div class="markdown-body article-detail" v-html="html"></div>
            <!-- 提前加载高亮样式 -->
            <mavon-editor
                codeStyle="atelier-plateau-light"
                style="display:none"
            />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            blog: {
                html: ""
            },
            likes: []
        };
    },
    mounted() {
        let id = this.$route.params.id;
        this.getBlogById(id);
    },
    computed: {
        html() {
            return this.blog.html;
        },
        userid() {
            return this.blog.userid || "default.png";
        },
        liked() {
            return this.likes.includes(this.$store.state.user.user._id);
        }
    },
    methods: {
        async getBlogById(id) {
            let blog = await this.$store.dispatch("getBlogById", id);
            this.blog = blog;
            this.likes = blog.likes;
        },
        handleEdit() {
            this.$router.push({
                path: "/add-blog?type=edit&id=" + this.blog._id
            });
        },
        async handleLiked() {
            let likes = await this.$store.dispatch("toggleLike", {
                blogID: this.blog._id,
                userID: this.$store.state.user.user._id
            });
            this.likes = likes;
        }
    }
};
</script>

<style lang="scss" scoped>
.blog-show {
    height: 100%;
    padding-top: 44px;
    width: 100%;
    margin-bottom: 12px;

    @media screen and (max-width: 500px) {
        width: 100vw;
        height: 100vh;
        margin: 0;
        padding: 0;
        .show-header {
            display: none;
        }
        .time {
            // position: absolute;
        }
        .article-edit {
            display: none;
        }
        .article {
            div.article-header {
                padding: 0 !important;
            }
        }
    }
    .article {
        padding: 0 0.5rem;
        margin: 0 auto;
        box-sizing: border-box;
        margin-top: 12px;
        max-width: 962px;
        background-color: #fff;
        width: 100%;
        div.article-header {
            padding: 1rem;
            box-sizing: border-box;
            z-index: 9;
            background-color: #fff;
            width: 100%;
            div.title {
                letter-spacing: 0;
                line-height: 40px;
                font-size: 24px;
                font-weight: bold;
                margin: 18.76px 0;
            }

            div.article-author-edit {
                font-size: 14px;
                height: 40px;
                padding: 0.5rem;
                border-bottom: 1px solid #f5f6f7;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                flex-wrap: wrap;
                div.article-author {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    position: relative;
                    .img {
                        margin-right: $uni-spacing-col-sm;
                        width: 30px;
                        height: 30px;
                        img.ueser-head {
                            width: 30px;
                            height: 30px;
                            border-radius: $uni-border-radius-circle;
                        }
                    }
                    span {
                        margin-right: $uni-spacing-col-base;
                        display: block;
                    }
                    .user-name-time {
                        display: flex;
                        flex-direction: column;
                        span.user-name {
                            white-space: nowrap;
                            margin-bottom: 4px;
                            font-size: 16px;
                            font-weight: bold;
                        }

                        span.time {
                            font-size: 12px;
                            white-space: nowrap;
                            color: $uni-text-color-grey;
                        }
                    }

                    span.views {
                        white-space: nowrap;
                        color: $uni-color-primary;
                        font-size: 14px;
                        &::before {
                            color: $uni-text-color;
                            font-size: 16px;
                        }
                    }

                    span.like {
                        white-space: nowrap;
                        cursor: pointer;
                        color: $uni-text-color;
                        &::before {
                            color: $uni-color-error;
                            font-size: 16px;
                        }
                    }
                }

                div.article-edit {
                    padding: 0 4px;
                    letter-spacing: 1px;
                    cursor: pointer;
                    color: $uni-color-primary;
                    &:hover {
                        color: $uni-color-error;
                    }
                }
            }
        }
        .article-detail {
            background-color: #fff;
            padding: 1rem;
        }
    }
}
</style>