<template>
    <div class="info">
        <h1 class="title">个人信息</h1>
        <div class="info-detail">
            <div class="img">
                <img :src="$store.state.user.user.avatar" />
                <p @click.stop="chooseIMG">修改头像</p>
            </div>
            <div class="info-container">
                <ul class="detail-container">
                    <li
                        v-for="(value, key) in $store.state.user.user"
                        :key="key"
                    >
                        <p>
                            <span class="first">{{ key | user }}</span> :
                            <span
                                class="last"
                                v-if="typeof value === 'number'"
                                >{{ value | date1 }}</span
                            >
                            <span v-else>
                                {{ value }}
                            </span>
                        </p>
                    </li>
                </ul>
            </div>
        </div>
        <input
            type="file"
            @change="changeAvatar"
            ref="upload"
            accept="image/*"
            class="upload"
            name="avatar"
        />
    </div>
</template>

<script>
export default {
    methods: {
        changeAvatar(e) {
            let file = e.target.files[0];
            if (file) {
                let formData = new FormData();
                formData.append("file", file);
                this.$store.dispatch("changeAvatar", formData);
            }
        },
        chooseIMG() {
            this.$refs.upload.click();
        }
    },
    mounted() {}
};
</script>

<style lang="scss" scoped>
div.info {
    width: 100%;
    height: 100%;
    position: relative;
    .upload {
        width: 0;
        height: 0;
        position: absolute;
        z-index: 0;
    }
    h1.title {
        font-weight: bold;
        font-size: 20px;
        height: 90px;
        line-height: 90px;
        border-bottom: 1px solid $uni-bg-color-grey-more;
    }

    div.info-detail {
        display: flex;
        flex-direction: row;
        margin-top: 1rem;
        div.img {
            width: 100px;
            margin-right: 1rem;
            img {
                width: 100px;
                height: 100px;
                border-radius: $uni-border-radius-circle;
            }
            p {
                width: 100%;
                color: $uni-color-primary;
                text-align: center;
                font-size: 14px;
                margin-top: 8px;
                cursor: pointer;
                &:hover {
                    color: $uni-color-warning;
                }
            }
        }

        div.info-container {
            width: 100%;
            ul.detail-container {
                li {
                    font-size: 14px;
                    color: $uni-text-color;
                    height: 36px;
                    line-height: 36px;
                    letter-spacing: 1px;
                    p {
                        .first {
                        }

                        .last {
                        }
                    }
                }
            }
        }
    }
    ol,
    ul {
        list-style: none;
    }
}
</style>