<template>
    <div @click.stop="handleView(blog._id)" class="my-page-blog-item">
        <div class="title column-item">
            <p>{{ blog.title }}</p>
        </div>
        <div class="detail column-item">
            <span> {{ blog.markdown | MarkdownCode }}</span>
        </div>
        <div class="handle-time column-item">
            <div class="time-views">
                <span class="time">{{ blog.createdAt | date1 }}</span>
                <span class="views"
                    >阅读数
                    <span class="views-target">{{ blog.views }}</span>
                </span>
                <span class="comments">
                    评论数
                    <span class="comments-target">
                        {{ blog.comments ? blog.comments.length : 0 }}
                    </span>
                </span>
            </div>
            <div class="handle">
                <span
                    v-show="edit"
                    @click.stop="handleEdit(blog._id)"
                    class="edit"
                    >编辑</span
                >
                <span
                    v-show="del"
                    @click.stop="handleDelete(blog._id)"
                    class="delete"
                    >删除</span
                >
                <span
                    v-show="Liked"
                    @click.stop="handleLiked(blog._id)"
                    class="liked"
                    >取消收藏</span
                >
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        blog: {
            type: Object,
            require: true
        },
        handleView: Function,
        handleLiked: Function,
        handleDelete: Function,
        handleEdit: Function,
        del: Boolean,
        edit: Boolean,
        Liked: Boolean
    }
};
</script>

<style lang="scss" scoped>
.my-page-blog-item {
    width: 100%;
    margin-bottom: 1px;
    letter-spacing: 1px;
    padding: 12px 24px;
    box-sizing: border-box;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
    cursor: pointer;
    border-bottom: 1px solid $uni-border-color;
    overflow: hidden;
    @extend .hover;

    &:first-of-type {
        border-top: 1px solid $uni-border-color;
    }

    &:hover {
        div.handle-time {
            div.handle {
                display: block;
            }
        }
    }
    .column-item {
        width: 100%;
    }
    div.title {
        margin-bottom: 6px;
        letter-spacing: 0;
        p {
            font-family: "Microsoft YaHei";
            font-weight: bolder;
            font-size: 18px;
            display: inline-block;
            word-break: break-all;
            color: $uni-text-color;
            font-size: 18px;
        }
    }

    div.detail {
        span {
            width: 100%;
            @extend .over2;
            line-height: 22px;
            font-size: 14px;
        }
    }

    div.handle-time {
        padding-top: 6px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        div.time-views {
            span.time {
                padding-right: 0.5rem;
                border-right: 1px solid $uni-bg-color-grey-more;
            }

            span.views {
                border-right: 1px solid $uni-bg-color-grey-more;
                padding: 0 0.5rem;
                span.views-target {
                    color: $uni-color-primary;
                    font-size: 14px;
                }
            }

            span.comments {
                padding: 0 0.5rem;
                span.comments-target {
                    color: $uni-color-primary;
                    font-size: 14px;
                }
            }
        }

        div.handle {
            display: none;
            font-size: 16px;
            letter-spacing: 1px;
            margin-right: 1rem;
            span.edit {
                padding: 0 3px;
                &:hover {
                    color: $uni-color-error;
                }
            }

            span.delete {
                border-left: 1px solid $uni-bg-color-grey-more;
                padding: 0 3px;
                &:hover {
                    color: $uni-color-error;
                }
            }
            .liked {
                letter-spacing: 0;
            }
            &:hover {
                color: $uni-color-warning;
            }
        }
    }
}
</style>