<template>
    <div class="main">
        <transition-group tag="ul" name="fade">
            <li
                v-for="item in middleBlogList"
                class="blog-item"
                :key="item._id"
                @click="handleView(item._id)"
            >
                <!-- <MyPageBlogItem :blog="item" /> -->
                <h3 class="blog-title column-item">
                    {{ item.title }}
                </h3>
                <p class="blog-detail column-item">
                    <span> {{ item.markdown | MarkdownCode }}</span>
                </p>
                <div class="blog-user-views">
                    <div class="anthor">
                        <a href="#">
                            <img
                                :src="baseIMGURL + item.userid + '?byuserid=1'"
                            />
                            <span>{{ item.username }}</span>
                        </a>
                    </div>
                    <div class="views-like">
                        <span
                            >收藏<span>{{ item.likes.length }}</span></span
                        >
                        <span
                            >阅读<span>{{ item.views }}</span></span
                        >
                        <span
                            >评论<span>{{ item.comments.length }}</span></span
                        >
                    </div>
                </div>
            </li>
        </transition-group>
    </div>
</template>

<script>
export default {
    props: {
        middleBlogList: Array
    },
    mounted() {},
    methods: {
        handleView(id) {
            this.$router.push("/show-blog/" + id);
        }
    }
};
</script>

<style lang="scss" scoped>
.main {
    margin-right: 12px;
    background-color: #fff;
    width: 760px;
    .blog-item {
        color: $uni-text-color;
        font-size: 16px;
        letter-spacing: 1px;
        background-color: #fff;
        padding: 18px 24px 13px 24px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        border-bottom: 1px solid $uni-border-color;
        position: relative;
        -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
        cursor: pointer;
        overflow: hidden;
        @extend .hover;

        .blog-title {
            font-size: 18px;
            font-weight: bold;
            color: #3d3d3d;
            margin-top: 0;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            letter-spacing: 0;
            white-space: nowrap;
        }
        .blog-detail {
            span {
                font-size: 14px;
                line-height: 22px;
                @extend .over2;
            }
        }
        .column-item {
            margin-bottom: 5px;
        }
        div.blog-user-views {
            height: 24px;
            margin-top: 2px;
            font-size: 16px;
            color: $uni-text-color;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            div.anthor {
                height: 24px;
                a {
                    height: 24px;
                    display: flex;
                    flex-direction: row;
                    align-items: center;

                    img {
                        width: 24px;
                        height: 24px;
                        border-radius: $uni-border-radius-circle;
                        margin-right: 6px;
                    }

                    span {
                        font-size: 14px;
                        color: #3d3d3d;
                    }
                }
            }

            div.views-like {
                & > span {
                    font-size: 12px;
                    color: $uni-text-color;
                    padding: 0 5px;
                    &:nth-of-type(2) {
                        border-left: 1px solid $uni-bg-color-grey-more;
                        border-right: 1px solid $uni-bg-color-grey-more;
                    }
                    span {
                        color: $uni-color-primary;
                    }
                    &::before {
                        color: $uni-text-color;
                        font-size: 16px;
                        font-weight: bold;
                    }
                }
            }
        }
    }
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
}
</style>