<template>
    <div class="home">
        <HomeHeader />
        <router-view></router-view>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    beforeRouteLeave(to, from, next) {
        if (to.name === "add-blog") {
            let token = localStorage.getItem("BLOG_TOKEN") ? true : false;
            if (!token) {
                this.$message({
                    type: "error",
                    message: "请先登录!"
                });
                return;
            }
        }
        next();
    },
    methods: {}
};
</script>

<style lang="scss" scoped>
.home {
    box-sizing: border-box;
    padding-top: 44px;
  
}
</style>