<template>
    <div class="home-index">
        <HomeIndexLeftNav :leftNavList="leftNavList" :handleItem="handleItem" />
        <HomeIndexMiddle :middleBlogList="$store.state.blog.indexBlogs" />
        <HomeIndexRight />
    </div>
</template>

<script>
export default {
    data() {
        return {
            leftNavList: [
                {
                    id: 1,
                    title: "前端",
                    value: "/",
                    isActive: true
                },
                {
                    id: 2,
                    title: "后端",
                    value: "/",
                    isActive: false
                },
                {
                    id: 3,
                    title: "python",
                    value: "/",
                    isActive: false
                },
                {
                    title: "javascript",
                    id: 4,
                    value: "/",
                    isActive: false
                },
                {
                    title: "java",
                    id: 5,
                    value: "/",
                    isActive: false
                }
            ],
            middleBlogList: []
        };
    },
    created() {
        this.$store.dispatch("getIndexPageBlog");
    },
    watch: {
        $route: {
            handler: function(to, from) {
                //根据key 获取类型
                if (!to) {
                    let key = from.query.key;
                } else {
                    let key = to.query.key;
                    if (!key) return;
                    this.leftNavList.forEach(v =>
                        v.title === key
                            ? (v.isActive = true)
                            : (v.isActive = false)
                    );
                }
            },
            immediate: true
        }
    },
    methods: {
        handleItem(e) {
            let key = e.target.dataset.key;
            this.leftNavList.forEach(v => {
                if (v.id == key) {
                    if (v.isActive === true) return;
                    v.isActive = true;
                    this.$router.push("/home?key=" + v.title);
                } else {
                    v.isActive = false;
                }
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.home-index {
    display: flex;
    width: 1180px;
    margin: 0 auto;
    flex-direction: row;
    position: relative;
    box-sizing: border-box;
    padding: 12px 0;
}
</style>