<template>
    <div class="my-page-blog">
        <transition-group tag="ul" name="fade">
            <li v-for="item in $store.state.blog.myBlogs" :key="item._id">
                <MyPageBlogItem
                    :blog="item"
                    :del="true"
                    :edit="true"
                    :handleView="handleView"
                    :handleDelete="handleDelete"
                    :handleEdit="handleEdit"
                /></li
        ></transition-group>
    </div>
</template>

<script>
export default {
    props: {
        handleView: Function
    },
    data() {
        return {
            something: ""
        };
    },
    // activated() {
    //     this.$store.dispatch("getMyBlog");
    // },
    mounted() {
        this.$store.dispatch("getMyBlog");
    },
    methods: {
        handleDelete(id) {
            this.$messageBox("此操作将永久删除该文件, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(async () => {
                    let msg = await this.$store.dispatch("deleteBlog", id);
                    let { ok } = msg;
                    if (ok === 1) {
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                        this.$store.commit("DELETEMYBLOG", id);
                    } else {
                        throw new Error("删除失败");
                    }
                })
                .catch(e => {
                    this.$message({
                        type: "info",
                        message:
                            e.toString() === "cancel"
                                ? "已取消删除"
                                : e.toString()
                    });
                    return;
                });
        },
        handleEdit(id) {
            this.$router.push({
                path: "/add-blog?type=edit&id=" + id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.my-page-blog {
    width: 100%;
    height: 100%;
    ul {
        list-style: none;
    }
}
</style>