<template>
    <div class="my-page-like">
        <transition-group tag="ul" name="fade">
            <li v-for="item in $store.state.blog.myLikedBlogs" :key="item._id">
                <MyPageBlogItem
                    :blog="item"
                    :handleView="handleView"
                    :handleLiked="handleLiked"
                    :liked="true"
                />
            </li>
        </transition-group>
    </div>
</template>

<script>
export default {
    props: {
        handleView: Function
    },
    data() {
        return {
            blogs: []
        };
    },
    // activated() {
    //     this.getMyLiked();
    // },
    mounted() {
        this.getMyLiked();
    },
    methods: {
        async getMyLiked() {
            await this.$store.dispatch("getMyliked");
        },
        async handleLiked(id) {
            let likes = await this.$store.dispatch("toggleLike", {
                blogID: id,
                userID: this.$store.state.user.user._id
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.my-page-like {
    width: 100%;
    height: 100%;
    ul {
        list-style: none;
    }
}
</style>