<template>
    <div class="search-tool">
        <div class="search-inner">
            <input
                @input="onInput"
                @focus="onFocus"
                @blur="onBlur"
                v-model.trim="value"
                type="text"
                placeholder="请输入"
            />
            <span
                @click.stop="handleSearch"
                :class="{ onfocus: onfocus }"
                class="iconfont icon-sousuo"
            ></span>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            onfocus: false,
            value: ""
        };
    },
    created() {},
    methods: {
        onInput(e) {
            this.$store.commit("SET_SEARCH_VALUE", e.target.value.trim());
        },
        onFocus() {
            this.onfocus = true;
        },
        onBlur() {
            this.onfocus = false;
        },
        handleSearch() {
            this.$emit("handleSearch", this.value);
        }
    }
};
</script>

<style lang="scss" scoped>
.search-tool {
    width: 100%;
    height: 100%;
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    align-items: center;
    .search-inner {
        height: 32px;
        display: flex;
        box-sizing: border-box;
        flex-direction: row;
        align-items: center;
        width: 100%;
        border-radius: $uni-border-radius-sm;
        background-color: #f0f0f5;
        input {
            margin: 0;
            padding: 0;
            width: 100%;
            outline: none;
            background-color: #f0f0f5;
            border-radius: $uni-border-radius-sm;
            border: 0;
            height: 100%;
            box-sizing: border-box;
            padding-left: 0.5rem;
        }
        span {
            box-sizing: border-box;
            white-space: nowrap;
            border-radius: 0 $uni-border-radius-sm $uni-border-radius-sm 0;
            height: 100%;
            padding: 0 0.5rem;
            background-color: #e8e8ee;
            line-height: 32px;
            cursor: pointer;
            transition: $transition-sm;
        }
        .onfocus {
            color: white;
            background-color: $uni-color-error;
        }
    }
}
</style>